import axios from 'axios'
import router from "../router/router"
import { Toast } from "vant";
import { getToken } from './auth'

// create an axios instance
const service = axios.create({
  // baseURL: "http://172.16.0.182:8080/", // api 的 base_url
  timeout: 200000, // request timeout
  withCredentials: true // 允许携带cookie
})

// request interceptor
service.interceptors.request.use(
  config => {
      
      let token = getToken()
      
    // Do something before request is sent
    if (typeof token!=="object") {
      // 让每个请求携带token-- ['Authorization']为自定义key 请根据实际情况自行修改
      config.headers['Authorization'] ="Bearer "+ token
    }else{
      if(window.location.pathname!=="/manage/login"){
        // console.log("http",window.location.pathname)
        window.location = "/manage/login?redirect=" + window.location.pathname
      }
      // console.log(token.name,token.message,token.stack)   
    }
    return config
  },
  error => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  response => response,
  /**
   * 在这里可以通过在response里，自定义code来标示请求状态
   * 根据code返回值说明权限有问题，登出并返回到登录页
   * 如想通过 xmlhttprequest 来状态码标识 逻辑可写在下面error中
   */
  error => {
    console.log('err'+error) // for debug
    if (error.response) {
          if (error.response.status===403) {
                  // 跳转无权限页面
                  // router.push("/noauthorized")
          }else if(error.response.status===401){
                //   用户密码错误
                //   window.location ="/noauthorized"
          }else{
            // Message({
            //   message: error.message,
            //   type: 'error',
            //   duration: 5 * 1000
            // })
          }
          // Toast(error.message||"发生未知错误");

      }
      return Promise.reject(error)
  }
)

export default service
