import Vue from 'vue';
import App from './App';
import router  from './router/router';
import store from './store/index';
import 'amfe-flexible';
import { Dialog } from 'vant';
import VueCookies from 'vue-cookies';

Vue.use(VueCookies)
Vue.use(Dialog);

new Vue({
  router,
  el: '#app',
  store,
  render: h => h(App)
});
