import { personalInfo } from "../../api/user"

const user = {
  state: {
    name: '',
    email: '',
    face: '',
    avatar: ''
  },

  mutations: {
    SET_EMAIL: (state, email) => {
      state.email = email
    },
    SET_FACE: (state, face) => {
      state.face = face
    },
    SET_NAME: (state, name) => {
      state.name = name
    },
    SET_AVATAR: (state, avatar) => {
      state.avatar = avatar
    }
  },

  actions: {
    // 获取用户信息
    GetUserInfo({
      commit,
      state
    }) {
      return new Promise((resolve, reject) => {
        personalInfo().then(response => {
          if (!response.data) { // 由于mockjs 不支持自定义状态码只能这样hack
            reject('error')
          }
          const data = response.data
          // console.log(data.access)
          if (data.data) {
            commit('SET_NAME', data.data.name)
            commit('SET_AVATAR', data.data.avatar)
            commit('SET_FACE', data.data.face)
            commit('SET_EMAIL', data.data.email)
            resolve(response)
          } else {
            reject('getInfo: no personalInfo !')
          }

        }).catch(error => {
          reject(error)
        })
      })
    }
  }
}

export default user
