<template>
    <div id="app">
        <transition v-if="loaded" name="van-fade">
            <router-view :class="{'app-unloaded': !loaded}"></router-view>
        </transition>
    </div>
</template>

<script>
import { checkToken , logout } from "./utils/auth";
import { Toast } from "vant";
import bottomSidebar from "./view/layout/bottomSidebar";

export default {
  name: "app",
  components: {
    [Toast.name]: Toast,
    "bottom-sidebar": bottomSidebar
  },
  data() {
    return {
      loaded: false,
      loadingInstance: undefined
    };
  },
  watch: {
    loaded: function(value) {
      if (value) this.loadingInstance.clear();
    }
  },
  mounted() {
    this.loadingInstance = Toast.loading({
      duration: 0, // 持续展示 toast
      forbidClick: true, // 禁用背景点击
      loadingType: "spinner"
    });
    if (window.location.pathname == "/manage/login") {
      this.loaded = true;
    } else {
      // console.log("开始验证登录信息");
      // this.$router.push("/manage/laborCosts");
      // this.loaded = true;
      checkToken()
        .then(res => {
          let verify = res;
          if (!verify) {
            logout();
            window.location = "/manage/login?redirect=" + window.location.pathname;
            // 躲开路由切换的动画时间。。。
            // console.log("登录验证失败");
            setTimeout(() => (this.loaded = true), 300);
          } else {
            // this.$store.dispatch("GetUserInfo")
            if (window.location.pathname == "/") {
              this.$router.push("/manage/laborCosts");
            }
            this.loaded = true;
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
<style>
html,
body,
#app {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  overflow: hidden;
  font-size: 1rem;
}

#app {
  font-family: "等线", "微软雅黑", "Microsoft YaHei", "Helvetica Neue",
    Helvetica, "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.app-loading {
  line-height: 100%;
  text-align: center;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.app-unloaded {
  opacity: 0 !important;
  display: none;
}

.app-enter-active,
.app-leave-active {
  transition: all 0.3s ease;
}

.app-leave-to,
.app-enter {
  opacity: 0;
  background: #fff;
}
</style>
