<template>
    <van-tabbar v-model="activeItem" v-if="true" :z-index="5" class="bottombar">
        <van-tabbar-item icon="shop" to="/manage/products">产品</van-tabbar-item>
        <van-tabbar-item  to="/manage/laborCosts" icon="contact">人力</van-tabbar-item>
        <van-tabbar-item icon="setting" to="/manage/user">设置</van-tabbar-item>
    </van-tabbar>
</template>
<script>
import { Tabbar, TabbarItem } from "vant";
export default {
  components: {
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem
  },
    created(){
      if(this.$route.name==="Products"){
          this.activeItem=0
      }else if(this.$route.name==="LaborCosts"){
          this.activeItem=1
      }else if(this.$route.name==="User"){
          this.activeItem=2
      }   
  },
  watch:{
    route(newV){
      if(newV.name==="Products"){
          this.activeItem=0
      }else if(newV.name==="LaborCosts"){
          this.activeItem=1
      }else if(newV.name==="User"){
           this.activeItem=2
      } 
    }
  },
  computed:{
    route(){
        return this.$route
      } 
    },
  data() {
    return {
      activeItem: 0
    };
  }
};
</script>
<style scoped>
.bottombar{
  height: 1.3rem;
}
</style>
