import { userlogin , verify } from "../api/login"
import MD5 from 'md5'
import VueCookies from 'vue-cookies';

export const logout = () => {
    if (VueCookies.get("token")) {
        VueCookies.remove("token")
    }
}

export const login = async (user, pass) => {
    return new Promise((resolve, reject) => {
        userlogin({
            email: user,
            password: MD5(pass)
        }).then((succ) => {
            resolve(succ.data)
        }, (err) => {
            // reject(err.response ? err.response.data : err)
            reject(err.response ? err.response.data : new Error("连接服务器失败"))
        })
    })
}

export const saveLogin = (token) => {
    logout()
    VueCookies.set("token", token, "1D")
}

export const getToken = () => {
    return VueCookies.get("token") || new Error("未找到Token")
}

export const checkToken = async () => {
    return new Promise(async (resolve, reject) => {
        let token = getToken()
        if (token) {
            let result = await verifyToken(token)
            resolve(result)
        } else {
            let result = false
            resolve(result)
        }
    })
}

export const verifyToken = async (token) => {
    return new Promise((resolve, reject) => {
        let result = null
        verify({
            token: token
        }).then((succ) => {
            resolve(true)
        }, (err) => {
            resolve(false)
        })
    })
}