import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

const router = new Router({
  mode: "history",
  routes: [{
      path: "/manage/login",
      name: "Login",
      component: resolve => require(['../view/login/login'], resolve),
      meta: {
        routename: [{
          name: "登录",
          link: "/manage/login"
        }]
      }
    },
    {
      path: "/",
      redirect: to => {
        return "/manage/laborCosts"
      }
    },
    {
      path: "/manage/",
      redirect: to => {
        return "/manage/laborCosts"
      },
      component: resolve => require(['../view/layout/layout'], resolve),
      children: [{
          path: "/manage/user",
          name: "User",
          component: resolve => require(['../view/user/user'], resolve),
          meta: {
            routename: [{
              name: "用户设置"
            }]
          }
        },
        {
          path: "/manage/laborCosts",
          name: "LaborCosts",
          component: resolve => require(['../view/laborCosts/laborCosts'], resolve),
          meta: {
            routename: [{
              name: "人力成本统计"
            }]
          },
        },
        {
          path: "/manage/products",
          name: "Products",
          component: resolve => require(['../view/products/products'], resolve),
          meta: {
            routename: [{
              name: "产品信息"
            }]
          }
        },
        {
          path: "/manage/laborCosts/*",
          redirect: to => {
            return "/manage/laborCosts"
          }
        },
        {
          path: "/manage/user/*",
          redirect: to => {
            return "/manage/user"
          }
        },
        {
          path: "/manage/products/*",
          redirect: to => {
            return "/manage/products"
          }
        } 
      ]
    },
    {
      path: "*",
      name: "找不到啦",
      component: resolve => require(['../view/notFound/notFound'], resolve)
    }
  ]
})

router.beforeEach((to, from, next) => {
  document.title = "友山基金"
  next();
});
export default router